import React,{useContext} from 'react'
import '../Nav/nav.css'
import {Link} from 'react-router-dom';
import {CartContext} from '../../Global/CartContext'
// import {Link} from 'react-scroll';

const NavBar = () => {

    const {qty} = useContext(CartContext)

    return (
        <div className="nav-bar">
            <div className="logo">
                <a href="#main"> 
                <div className="pizza">Pizza</div>
                <div className="Factory">FACTORY</div>
               </a>
            </div>
            <div className="navigation-links">
                <ul>
                    <li><Link className='menu-nav' to='/' >Home</Link>  </li>
                    <li><a href="#about">About </a> </li>
                    <li><a href="#services">Services </a> </li>
                    <li><a href="#contact">Contact </a> </li>
                    <li><Link className='menu-nav' to='/Menu'>Menu</Link></li>
                    <li><Link className='menu-nav' to='/Cart'><span className="shoppingCart"><i className="fas fa-cart-plus"></i> <span className="cartCount">{qty}</span> </span></Link> </li>
                </ul>
            </div>
        </div>
    )
}

export default NavBar;
