export const CartReducer = (state,action)=>{    // state is coming from cardContext and action is coming from products.jsx
 
    const {shoppingCart, totalPrice, qty}  = state; // destructuring the object from card context.
    
    let product;
    let index;
    let updatedPrice;
    let updatedQty;
    
    switch(action.type){ // action is from products.jsx
        case 'ADD_TO_CART':
            const check = shoppingCart.find(product=>product.id===action.id); // action.id is from products.jsx // console.log('add to cart'); check this way
            if(check){
                alert('Pizza added to cart, you can increase quantity in the cart Section ')
                return state;
            }else{
                product = action.product;
                product['qty'] = 1;
                updatedQty = qty+1;
                updatedPrice = totalPrice+product.price;
                return {shoppingCart:[product ,...shoppingCart], totalPrice: updatedPrice, qty:updatedQty  }
            }
            break;
        case 'INC':
            product = action.cart; // action refering from cart.js
            product.qty = product.qty + 1;
            updatedPrice = totalPrice + product.price;
            updatedQty = qty + 1;
            index = shoppingCart.findIndex(cart=> cart.id=== action.id);
            shoppingCart[index] = product;
            return {shoppingCart:[...shoppingCart], totalPrice: updatedPrice, qty: updatedQty}
            break;
        
        case "DEC":
            product = action.cart;
            if(product.qty>1){
                product.qty = product.qty-1;
                updatedPrice = totalPrice - product.price;
                updatedQty = qty - 1;
                index = shoppingCart.findIndex(cart=> cart.id === action.id);
                shoppingCart[index] = product;
                return {shoppingCart:[...shoppingCart], totalPrice:updatedPrice, qty : updatedQty}
            }else{
                return state;
            }
            break;
        
        case 'DELETE':
            const filtered = shoppingCart.filter(product=> product.id !== action.id);
            product = action.cart;
            updatedQty = qty - product.qty;
            updatedPrice = totalPrice - product.price* product.qty;
            return {shoppingCart:[...filtered], totalPrice: updatedPrice, qty: updatedQty }
            break;
        default:
            return state;
    }
}