import React from 'react'
import '../Contact/Contact.css'
function Contact() {


    const details=[
        {icon:<i class="fas fa-phone"></i>,h4:'+91 987654321',p:'Call for immediate orders'},
        {icon:<i class="fas fa-search-location"></i>,h4:'198 Brigade Road',p:'Suite 721 Brocade Enclave Bangalore'},
        {icon:<i class="far fa-clock"></i>,h4:'Open from Monday-Friday',p:'8:00am - 9:00pm'},

    ]


    return (
        <div className="details-container">
            <div className="details-left">
                {
                    details.map(detail=>(
                        <div className='details-card'>
                        <div className="details-icon">
                            {detail.icon}
                        </div>
                        <div className="details-info">
                        <h4 className="details-h4" >{detail.h4}</h4>
                        <p className="details-p">{detail.p}</p>
                        </div>
                        </div>
                    ))
                }
            </div>
            <div className="details-right">
                <div className="details-icons">
                <i class="fab fa-twitter"></i>
                <i class="fab fa-facebook-f"></i>
                <i class="fab fa-instagram"></i>
                </div>
            </div>
        </div>
    )
}

export default Contact
