import React from 'react'
import '../CallSite/CallSite.css';
import {Link} from 'react-router-dom'

function CallSite() {
    return (
        <div className="call-container">
            <h3 className="special" >Today's Special</h3>
            <p className='special-p' >Dont Forget to check Out our Menu</p>
            <Link to='/Menu'><button className='special-btn' >Menu</button></Link> 
        </div>
    )
}

export default CallSite
