import React,{useContext} from 'react'
import { MenuContext } from '../../Global/MenuContext';
import {CartContext} from '../../Global/CartContext'

import '../Menu/menu.css'

const Menu = () => {

    const {products} = useContext(MenuContext);
    const {dispatch} = useContext(CartContext); 
    return (
        <div className='menu' >
            <div className="menu-container" >
            {
                products.map((product)=>(
            <div className="pizza-container" key={product.id} >
                
                <div className="pizza-image">
                    <img src={product.image} alt=""/>
                </div>
                <div className="pizza-info">
                    <div className="pizza-name">
                    {product.name}
                    </div>
                    <div className="pizza-desc">
                    {product.desc}
                    </div>  
                    <div className="order-price">
                    ₹{product.price}.00
                    </div> 
                    <div className="order-btn" onClick={()=> dispatch({type:'ADD_TO_CART',id:product.id, product})}>Order</div>
                </div>
            </div>        
                ))
            }                
        </div>
        </div>
    )
}

export default Menu;
