import React,{useContext} from 'react';
import {CartContext} from '../../Global/CartContext';
import StripeCheckout from 'react-stripe-checkout'
import '../Cart/Cart.css'

import '../Cart/Cart.css';
const Cart = () => {
    const {shoppingCart, totalPrice, qty, dispatch} = useContext(CartContext);
    const handleToken=(token)=>{
        
    }
    return (
        <div className="cart" >
            <h2 className='cart-h2' >Click on Menu to add more pizza's</h2>
            <div className="cart-container">
            <div className="cart-details" style={{marginTop:'100px'}} >
            {/* {shoppingCart.length>0 ? 'products' : 'sorry your cart is currently empty' } */}
            {shoppingCart.length>0 ?
            shoppingCart.map((cart)=>(
                <div className="cart" key={cart.id}>
                    <span className="cart-image"><img src={cart.image} alt="not found"/></span>
                    <span className="cart-product-name">{cart.name}</span>
                    <span className="cart-product-price">₹{cart.price}.00</span>
                    <span className='inc' onClick={()=> dispatch({type:'INC', id:cart.id, cart}) } ><i className="fas fa-plus"></i> </span>
                    <span className='product-quantity'>{cart.qty}</span>
                    <span className='dec' onClick={()=> dispatch({type:'DEC', id:cart.id,cart})} ><i className="fas fa-minus"></i></span>
                    <span className='product-total-price'>₹{cart.price * cart.qty}.00</span>
                    <span className='delete-product' onClick={()=>dispatch({type:"DELETE", id:cart.id,cart})} ><i className="fas fa-trash-alt"></i></span>
                </div>
                ))
             : 'Cart is currently empty, order from menu section' }
            </div>
            {shoppingCart.length>0 ? <div className="cart-summary">
            <div className="summary">
                <h3>Cart Summary</h3>
                <div className="total-items">
                    <div className="items">Total Items</div>
                    <div className="items-count">{qty}</div>
                </div>
                <div className="total-price-section">
                    <div className="just-title">Total Price</div>
                    <div className="items-price">₹{totalPrice}.00</div>
                </div>
                <div className="stripe-section">
                    {/* {stripe button} */}
                    <StripeCheckout 
                    stripeKey = "pk_test_51IUp78EACUfs4RuAkgJY6Vpq4y9hQ9fAVyJEtyOYeA6iHyU5paZnT5arABpIUDyvGabXAO2yXJZdFIzMJB9Kcb9H00kvlVogtY"
                    token={handleToken}
                    billingAddress
                    shippingAddress
                    amount={totalPrice * 100}
                    name='All Products'
                    >
                    </StripeCheckout>
                </div>
            </div>
            </div> : ''}
            </div>
    </div>
    )
}

export default Cart;
