import React from 'react'
import '../Services/Service.css'

function Services() {

    const services = [
        {icon:<i class="fas fa-cheese"></i>, name:"HEALTY FOOD",p:'Cooked with fresh ingreadients'},
        {icon:<i class="fas fa-biking"></i>, name:"HOME DELIVERY",p:'Order Pizza online and get Delivered'},
        {icon:<i class="fas fa-pizza-slice"></i>, name:"ORIGINAL RECIPES",p:'Cooked by trained Chefs'}
    ]

    return (
        <div className='service-container' id='services' >
            <div className="service-heading">
                <h2>OUR SERVICES</h2>
            </div>
            <div className='service-card'>
            {
                services.map(services=>(
                    <div className="serve">
            <div className="service-logo">
                {services.icon}
            </div>
            <h4 className="service-name">{services.name}</h4>
            <p className='service-p' >{services.p}</p>
            </div>
            ))
            }
            </div>
        </div>
    )
}

export default Services
