import React from 'react'
import '../Banner/banner.css'
import '../../App.css'
const Banner = () => {
    return (
        <div className="banner-container"  id="main" >
            <div className="container">
            <h2 className="intro-message">Welcome to <span className="Pizza-factory">Pizza Factory</span> </h2>
            <h3 className="intro-desc" >There’s no better feeling in the world than a warm pizza box in your lap</h3>
            </div>
        </div>
    )
}
export default Banner
